<template>
    <div class='b-product'>
        <component-con :modules-index="modulesIndex" :component-name="componentName" :setting="setting">
            <div class="form-group">
                <label class="group-l">
                    风格：
                </label>
                <radio-group v-model="styleSel" size="small">
                    <radio v-for="item in data.showStyle" :key="item" :label="item.key">{{ item.title }}</radio>
                </radio-group>
            </div>
            <template v-if="styleSel == '0'">
                <div class="form-group">
                    <label class="group-l">
                        提示：
                    </label>
                    <radio-group v-model="styleSel" size="small">
                        <p data-v-104e7af6="" data-v-378bd341="" class="ec-remark">*预览状态显示默认缩略图，正式发布可于前台查看正式效果</p>
                    </radio-group>
                </div>
                <div class="form-group">
                    <label class="group-l">
                        尺寸：
                    </label>
                    <radio-group v-model="sizeSel" size="small">
                        <radio v-for="item in data.showSize" :key="item" :label="item.key">{{ item.title }}</radio>
                    </radio-group>
                </div>
    
            </template>
            <div class="form-group">
                <label class="group-l">
                    显示标签：
                </label>
                <checkbox-group v-model="tagSelList" size="small">
                    <checkbox v-for="item in aShowtags" :key="item" :label="item.key">{{ item.title }}</checkbox>
                </checkbox-group>
            </div>
            <div class="form-group">
                <label class="group-l">
                    模块：
                </label>
                <radio-group v-model="moduleSel" size="small">
                    <radio v-for="item in data.showModule" :key="item" :label="item.key">{{ item.title }}</radio>
                </radio-group>
            </div>
            <div class="form-group">
                <label class="group-l">
                    商品数量：
                </label>
                <div class="group-r">
                    <ec-input type="number" size="small" min="1" v-model="number" placeholder="默认10张图片"></ec-input>
                    <p class="ec-remark">*图片数量最少1张，小于1默认为1张</p>
                </div>
            </div>
        </component-con>
    </div>
</template>

<script>
// custom components
import ComponentCon from '../element/ComponentCon'

// third party components
import {
    CheckboxGroup,
    Checkbox,
    Radio,
    RadioGroup,
    Input,
    DatePicker
} from 'element-ui'

// minxin
import formProcessing from 'mixins/form-processing'

// localData
import localData from './data/local'

export default {
    name: 'b-product',
    props: ['setting', 'onlineData', 'modulesIndex'],
    mixins: [formProcessing],
    data() {
        return {
            componentName: localData.componentName
        }
    },
    components: {
        Radio,
        Checkbox,
        CheckboxGroup,
        RadioGroup,
        ComponentCon,
        "EcInput": Input,
        DatePicker,
    },
    mounted() {

    },
    computed: {
        aShowtags() {
            let styleStyleIndex = 0
            this.data.showStyle.map((v, i) => {
                if (v.key == this.styleSel) styleStyleIndex = i
            })
            let aTag = this.data.showStyle[styleStyleIndex].tags
            let aShowTags = []
            this.data.showTag.map((v, i) => {
                aTag.map((tV, tI) => {
                    if (v.key == tV) {
                        aShowTags.push(v)
                    }
                })
            })
            return aShowTags
        },
        styleSel: {
            get() {
                return this.data.isStyleSel
            },
            set(value) {
                this.updateRadioSel('isStyleSel', value)
            }
        },
        sizeSel: {
            get() {
                return this.data.isSizeSel
            },
            set(value) {
                this.updateRadioSel('isSizeSel', value)
            }
        },
        tagSelList: {
            get() {
                return this.data.tagSelList
            },
            set(value) {
                this.updateRadioSel('tagSelList', value)
            }
        },
        moduleSel: {
            get() {
                return this.data.isModuleSel
            },
            set(value) {
                this.updateRadioSel('isModuleSel', value)
            }
        },
        scrollNumber: {
            get() {
                return this.data.allValue.scrollNumber
            },
            set(value) {
                this.updateTitleText('scrollNumber', value)
            }
        },
        number: {
            get() {
                return this.data.allValue.number < 1 ? 1 : this.data.allValue.number
            },
            set(value) {
                if (value < 1) {
                    value = 1
                }
                this.updateTitleText('number', Number(value))
            }
        },
        data() {
            return Object.assign({}, localData.data, this.onlineData)
        }
    },
    watch: {

    }
}

</script>

<style scoped>

</style>


